import { 
    TRANSACTION_LOADING,
    TRANSACTION_SUCCESS,
    TRANSACTION_ERROR,
    TRANSACTION_CODE_ERROR,
   RESTART_TRANSACTION_RESPONSE,
   RESTART_TRANSACTION_RETRY_RESPONSE,
   TRANSACTION_RETRY_LOADING,
   TRANSACTION_RETRY_SUCCESS,
   TRANSACTION_RETRY_ERROR,
   TRANSACTION_RETRY_CODE_ERROR
    } from "../ActionTypes/UserTransactionActionTypes"
    
    
    const initState = {
    transactionResponse:"",
    transactionRetryResponse:""
    }
    
    
    const TransactionReducer = (state=initState, action) =>{
    switch(action.type){ 
    
 
    case RESTART_TRANSACTION_RESPONSE:
    return {
        ...state,
        transactionResponse:""
    } 
    case TRANSACTION_LOADING:
        return {
        ...state,
        transactionResponse:'loading'
        }
    
    case TRANSACTION_SUCCESS:
    console.log(action)
    return {
        ...state,
        transactionResponse:action.res,
    }
    
    case TRANSACTION_ERROR:
        console.log(action)
    
    return {
            ...state,
            transactionResponse:action.res,
        }
    
    case TRANSACTION_CODE_ERROR:
    console.log(action)
    return {
    ...state,
    transactionResponse:'there seems to be a problem please refresh your browser',
            }




     case RESTART_TRANSACTION_RETRY_RESPONSE:
    return {
        ...state,
        transactionRetryResponse:""
    } 
    case TRANSACTION_RETRY_LOADING:
        return {
        ...state,
        transactionResponse:'loading'
        }
    
    case TRANSACTION_RETRY_SUCCESS:
    console.log(action)
    return {
        ...state,
        transactionRetryResponse:action.res,
    }
    
    case TRANSACTION_RETRY_ERROR:
        console.log(action)
    
    return {
            ...state,
           transactionRetryResponse:action.res,
        }
    
    case TRANSACTION_RETRY_CODE_ERROR:
    console.log(action)
    return {
    ...state,
    transactionRetryResponse:'there seems to be a problem please refresh your browser',
            }
          
        default:
            return state
    } 
    }
    
    export default TransactionReducer; 
    