import {combineReducers} from 'redux'
import changeState from './DrawerReducer';
import AuthReducer from './AuthReducer';
import AdminProfileReducer from './AdminProfileReducer';
import UserProfileReducer from './UserProfileReducer';
import BankDetailsReducer from './BankDetailsReducer';
import EventReducer from './EventReducer';
import TransactionReducer from './TransactionReducer';
import PercentageReducer from './PercentageReducer'





const RootReducer = combineReducers({
    drawer:changeState,
     auth:AuthReducer,
     adminProfile:AdminProfileReducer,
     userProfile:UserProfileReducer,
     bankDetails:BankDetailsReducer,
     eventReducer:EventReducer,
     transactionReducer:TransactionReducer,
     percentageReducer:PercentageReducer
});
  




export default RootReducer