
import { 
        RESTART_REGISTRATION_RESPONSE, 
        REGISTRATION_SUCCESS, 
        REGISTRATION_ERROR,
         REGISTRATION_CODE_ERROR, 
         REGISTRATION_LOADING,
         RESTART_ADMINLIST_RESPONSE,
         ADMINLIST_LOADING,
         ADMINLIST_SUCCESS,
         ADMINLIST_ERROR,
         ADMINLIST_CODE_ERROR,
         RESTART_CHANGE_PASSWORD_RESPONSE,
         CHANGE_PASSWORD_LOADING,
         CHANGE_PASSWORD_SUCCESS,
         CHANGE_PASSWORD_ERROR,
         CHANGE_PASSWORD_CODE_ERROR
        } from "../ActionTypes/AdminProfileTypes"

const initState = {
    adminRegistrationResponse:"",
    adminListResponse:"",
    changePasswordResponse:""
    }
     


    const AdminProfileReducer = (state=initState, action) =>{
        switch(action.type){
             /**
              * register new admin
              */
            case RESTART_REGISTRATION_RESPONSE:
                return {
                    ...state,
                    adminRegistrationResponse:""
                } 
                case REGISTRATION_LOADING :
                    return {
                        ...state,
                        adminRegistrationResponse:'loading'
                    }

          case REGISTRATION_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  adminRegistrationResponse:action.res,
              }
    
              case REGISTRATION_ERROR:
                    console.log(action)

              return {
                     ...state,
                     adminRegistrationResponse:action.res,
                    }
    
            case REGISTRATION_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                adminRegistrationResponse:'there seems to be a problem please refresh your browser',
                 }
             /**
              * view admin lists
              */
             case RESTART_ADMINLIST_RESPONSE:
                return {
                    ...state,
                    adminListResponse:""
                } 
                case ADMINLIST_LOADING :
                    return {
                    ...state,
                    adminListResponse:'loading'
                    }

          case ADMINLIST_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  adminListResponse:action.res,
              }
    
              case ADMINLIST_ERROR:
                    console.log(action)

              return {
                     ...state,
                     adminListResponse:action.res,
                    }
    
            case ADMINLIST_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                adminListResponse:'there seems to be a problem please refresh your browser',
                     }
             /**
              * change password
              */
             case RESTART_CHANGE_PASSWORD_RESPONSE:
                return {
                    ...state,
                    changePasswordResponse:""
                } 
                case CHANGE_PASSWORD_LOADING :
                    return {
                    ...state,
                    changePasswordResponse:'loading'
                    }

          case CHANGE_PASSWORD_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  changePasswordResponse:action.res,
              }
    
              case CHANGE_PASSWORD_ERROR:
                    console.log(action)

              return {
                     ...state,
                     changePasswordResponse:action.res,
                    }
    
            case CHANGE_PASSWORD_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                changePasswordResponse:'there seems to be a problem please refresh your browser',
                     }
                default:
                    return state
        }  
    }
    
    export default AdminProfileReducer; 
