import { 
    RESTART_LOGIN_RESPONSE, 
    LOGIN_LOADING, 
    LOGIN_SUCCESS, 
    LOGIN_ERROR, 
    LOGIN_CODE_ERROR, 
    RESTART_FORGOT_PASSWORD_RESPONSE,
    FORGOT_PASSWORD_LOADING,
    FORGOT_PASSWORD_SUCCESS,
    FORGOT_PASSWORD_ERROR,
    FORGOT_PASSWORD_CODE_ERROR,
    RESTART_RESET_PASSWORD_RESPONSE,
    RESET_PASSWORD_LOADING,
    RESET_PASSWORD_SUCCESS,
    RESET_PASSWORD_CODE_ERROR,
    RESET_PASSWORD_ERROR
    } from "../ActionTypes/AuthTypes"

const initState = {
    loginResponse:"",
    forgotPasswordResponse:"",
    resetPasswordResponse:""
    }
     


    const AuthReducer = (state=initState, action) =>{
        switch(action.type){
             /**
              * login 
              */
            case RESTART_LOGIN_RESPONSE:
                return {
                    ...state,
                    loginResponse:""
                } 
                case LOGIN_LOADING :
                    return {
                        ...state,
                        loginResponse:'loading'
                    }

          case LOGIN_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  loginResponse:action.res,
              }
    
              case LOGIN_ERROR:
                    console.log(action)

              return {
                     ...state,
                     loginResponse:action.res,
                    }
    
                case LOGIN_CODE_ERROR:
                      console.log(action)
                  return {
                        ...state,
                    loginResponse:'there seems to be a problem please refresh your browser',
                        }
                    /**
                     * forgot password
                     */
           case RESTART_FORGOT_PASSWORD_RESPONSE:
                return {
                    ...state,
                    forgotPasswordResponse:""
                } 
                case FORGOT_PASSWORD_LOADING :
                    return {
                        ...state,
                        forgotPasswordResponse:'loading'
                    }

          case FORGOT_PASSWORD_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  forgotPasswordResponse:action.res,
              }
    
              case FORGOT_PASSWORD_ERROR:
                    console.log(action)

              return {
                     ...state,
                     forgotPasswordResponse:action.res,
                    }
    
                case FORGOT_PASSWORD_CODE_ERROR:
                      console.log(action)
                  return {
                        ...state,
                    forgotPasswordResponse:'there seems to be a problem please refresh your browser',
                        }
                
            /**
                     * reset password
                     */
           case RESTART_RESET_PASSWORD_RESPONSE:
                return {
                    ...state,
                    resetPasswordResponse:""
                } 
                case RESET_PASSWORD_LOADING :
                    return {
                        ...state,
                        resetPasswordResponse:'loading'
                    }

          case RESET_PASSWORD_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  resetPasswordResponse:action.res,
              }
    
              case RESET_PASSWORD_ERROR:
                    console.log(action)

              return {
                     ...state,
                     resetPasswordResponse:action.res,
                    }
    
                case RESET_PASSWORD_CODE_ERROR:
                      console.log(action)
                  return {
                        ...state,
                    resetPasswordResponse:'there seems to be a problem please refresh your browser',
                        }
                          
    
                default:
                    return state
    
        }
    }
    
    export default AuthReducer;
