import {
     RESTART_USERLIST_RESPONSE, 
    USERLIST_SUCCESS, 
    USERLIST_ERROR, 
    USERLIST_CODE_ERROR, 
    USERLIST_LOADING, 
    RESTART_USERTYPES_RESPONSE,
    USERTYPES_LOADING,
    USERTYPES_SUCCESS,
    USERTYPES_ERROR,
    USERTYPES_CODE_ERROR,
    RESTART_USER_ACTIVATE_RESPONSE,
    USER_ACTIVATE_LOADING,
    USER_ACTIVATE_SUCCESS,
    USER_ACTIVATE_ERROR,
    USER_ACTIVATE_CODE_ERROR,
    RESTART_USER_ENABLE_RESPONSE,
    USER_ENABLE_LOADING,
    USER_ENABLE_SUCCESS,
    USER_ENABLE_ERROR,
    USER_ENABLE_CODE_ERROR,
    RESTART_SINGLE_USER_PROFILE,
    SINGLE_USER_PROFILE_SUCCESS,
    SINGLE_USER_PROFILE_ERROR,
    SINGLE_USER_PROFILE_CODE_ERROR,
    SINGLE_USER_PROFILE_LOADING
} from "../ActionTypes/UserProfileTypes"


const initState = {
    userTypeResponse:"",
    userListResponse:"",
    userActivationResponse:"",
    userEnableResponse:"",
    singleUserProfile:""
    }


    const UserProfileReducer = (state=initState, action) =>{
        switch(action.type){ 
         /**
              * view userlist
              */
             case RESTART_USERLIST_RESPONSE:
                return {
                    ...state,
                    userListResponse:""
                } 
                case USERLIST_LOADING :
                    return {
                    ...state,
                    userListResponse:'loading'
                    }

          case USERLIST_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  userListResponse:action.res,
              }
    
              case USERLIST_ERROR:
                    console.log(action)

              return {
                     ...state,
                     userListResponse:action.res,
                    }
    
            case USERLIST_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                userTypeResponse:'there seems to be a problem please refresh your browser',
                     }
             /**
              * view usertypes
              */
             case RESTART_USERTYPES_RESPONSE:
                return {
                    ...state,
                    userTypeResponse:""
                } 
                case USERTYPES_LOADING :
                    return {
                    ...state,
                    userTypeResponse:'loading'
                    }

          case USERTYPES_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  userTypeResponse:action.res,
              }
    
              case USERTYPES_ERROR:
                    console.log(action)

              return {
                     ...state,
                     userTypeResponse:action.res,
                    }
    
            case USERTYPES_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                userTypeResponse:'there seems to be a problem please refresh your browser',
                     }

            /**
              * view useractivated
              */
             case RESTART_USER_ACTIVATE_RESPONSE:
                return {
                    ...state,
                    userActivationResponse:""
                } 
                case USER_ACTIVATE_LOADING :
                    return {
                    ...state,
                    userActivationResponse:'loading'
                    }

          case USER_ACTIVATE_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  userActivationResponse:action.res,
              }
    
              case USER_ACTIVATE_ERROR:
                    console.log(action)

              return {
                     ...state,
                     userActivationResponse:action.res,
                    }
    
            case USER_ACTIVATE_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                userActivationResponse:'there seems to be a problem please refresh your browser',
                     }
            
        /**
              * view userenabled
              */
             case RESTART_USER_ENABLE_RESPONSE:
                return {
                    ...state,
                    userEnableResponse:""
                } 
                case USER_ENABLE_LOADING :
                    return {
                    ...state,
                    userEnableResponse:'loading'
                    }

          case USER_ENABLE_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  userEnableResponse:action.res,
              }
    
              case USER_ENABLE_ERROR:
                    console.log(action)

              return {
                     ...state,
                     userEnableResponse:action.res,
                    }
    
            case USER_ENABLE_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                userEnableResponse:'there seems to be a problem please refresh your browser',
                     }

           /**
              * view userenabled
              */
             case RESTART_SINGLE_USER_PROFILE:
                return {
                    ...state,
                    singleUserProfile:""
                } 
                case SINGLE_USER_PROFILE_LOADING :
                    return {
                    ...state,
                    singleUserProfile:'loading'
                    }

          case SINGLE_USER_PROFILE_SUCCESS:
              console.log(action)
              return {
                  ...state,
                  singleUserProfile:action.res,
              }
    
              case SINGLE_USER_PROFILE_ERROR:
                    console.log(action)

              return {
                     ...state,
                     singleUserProfile:action.res,
                    }
    
            case SINGLE_USER_PROFILE_CODE_ERROR:
                console.log(action)
                return {
                ...state,
                singleUserProfile:'there seems to be a problem please refresh your browser',
                     }

                default:
                    return state
        } 
    }
    
    export default UserProfileReducer; 
