import { RESTART_UPDATE_PERCENTAGE_RESPONSE, GET_PERCENTAGE_LOADING, 
    RESTART_GET_PERCENTAGE_RESPONSE, GET_PERCENTAGE_SUCCESS, GET_PERCENTAGE_ERROR,
     GET_PERCENTAGE_CODE_ERROR, UPDATE_PERCENTAGE_LOADING, UPDATE_PERCENTAGE_SUCCESS, 
     UPDATE_PERCENTAGE_ERROR, UPDATE_PERCENTAGE_CODE_ERROR } from "../ActionTypes/PercentageChargeActionTypes"

    
    
    const initState = {
     updatePercentageCharge:"",
     getPercentageCharge:""
    }
    
    
    const PercentageReducer = (state=initState, action) =>{
    switch(action.type){ 
    /**
     * view BANKLIST
     */
    case RESTART_GET_PERCENTAGE_RESPONSE:
        return {
            ...state,
            getPercentageCharge:""
        } 
        case GET_PERCENTAGE_LOADING:
            return {
            ...state,
            getPercentageCharge:'loading'
            }
    
    case  GET_PERCENTAGE_SUCCESS:
        console.log(action)
        return {
            ...state,
            getPercentageCharge:action.res,
        }
    
        case GET_PERCENTAGE_ERROR:
            console.log(action)
    
        return {
            ...state,
            getPercentageCharge:action.res,
            }
    
    case GET_PERCENTAGE_CODE_ERROR:
        console.log(action)
        return {
        ...state,
        getPercentageCharge:'there seems to be a problem please refresh your browser',
            }
    /**
        * view BANK_NAME
        */
    
        /**
        * view BANKLIST
        */
    case RESTART_UPDATE_PERCENTAGE_RESPONSE:
        return {
            ...state,
            updatePercentageCharge:""
        } 
        case UPDATE_PERCENTAGE_LOADING :
            return {
            ...state,
            updatePercentageCharge:'loading'
            }
    
    case  UPDATE_PERCENTAGE_SUCCESS:
        console.log(action)
        return {
            ...state,
            updatePercentageCharge:action.res,
        }
    
        case UPDATE_PERCENTAGE_ERROR:
            console.log(action)
    
        return {
                ...state,
                updatePercentageCharge:action.res,
            }
    
    case UPDATE_PERCENTAGE_CODE_ERROR:
        console.log(action)
        return {
        ...state,
        updatePercentageCharge:'there seems to be a problem please refresh your browser',
                }
 
        default:
            return state
    } 
    }
    
    export default PercentageReducer; 
    