import {
    RESTART_BANKLIST_RESPONSE, 
   BANKLIST_SUCCESS, 
   BANKLIST_ERROR, 
   BANKLIST_CODE_ERROR, 
   BANKLIST_LOADING, 
   RESTART_BANK_NAME_RESPONSE,
   BANK_NAME_LOADING,
   BANK_NAME_SUCCESS,
   BANK_NAME_ERROR,
   BANK_NAME_CODE_ERROR,

} from "../ActionTypes/UserBankDetailTypes"

  
const initState = {
   bankNameResponse:"",
   bankListResponse:"",
   }


   const BankDetailsReducer = (state=initState, action) =>{
       switch(action.type){ 
        /**
             * view BANKLIST
             */
            case RESTART_BANKLIST_RESPONSE:
               return {
                   ...state,
                   bankListResponse:""
               } 
               case BANKLIST_LOADING :
                   return {
                   ...state,
                   bankListResponse:'loading'
                   }

         case BANKLIST_SUCCESS:
             console.log(action)
             return {
                 ...state,
                 bankListResponse:action.res,
             }
   
             case BANKLIST_ERROR:
                   console.log(action)

             return {
                    ...state,
                    bankListResponse:action.res,
                   }
   
           case BANKLIST_CODE_ERROR:
               console.log(action)
               return {
               ...state,
               bankNameResponse:'there seems to be a problem please refresh your browser',
                    }
            /**
             * view BANK_NAME
             */
            case RESTART_BANK_NAME_RESPONSE:
               return {
                   ...state,
                   bankNameResponse:""
               } 
               case BANK_NAME_LOADING :
                   return {
                   ...state,
                   bankNameResponse:'loading'
                   }

         case BANK_NAME_SUCCESS:
             console.log(action)
             return {
                 ...state,
                 bankNameResponse:action.res,
             }
   
             case BANK_NAME_ERROR:
                   console.log(action)

             return {
                    ...state,
                    bankNameResponse:action.res,
                   }
   
           case BANK_NAME_CODE_ERROR:
               console.log(action)
               return {
               ...state,
               bankNameResponse:'there seems to be a problem please refresh your browser',
                    }

        

               default:
                   return state
       } 
   }
   
   export default BankDetailsReducer; 
