import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import './scss/style.scss';
import { Gaurd } from './guard/Guard';



const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const TheLayout = React.lazy(() => import('./containers/TheLayout'));

// Pages
const Login = React.lazy(() => import('./views/login'));
const ForgotPassword = React.lazy(()=>import('./views/forgotpassword'));
const ResetPassword = React.lazy(()=>import('./views/reset-password'));
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'));
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'));

class App extends Component {

  render() {
    return (
      <HashRouter>
          <React.Suspense fallback={loading}>
            <Switch>
              <Route exact path="/login" name="Login Page" render={props => <Login {...props}/>} />
              <Route exact path="/forgot-password" name="Forgot Password Page" render={props => <ForgotPassword  {...props}/>} />
              <Route exact path="/reset-password/:token" name="Reset Password Page" render={props => <ResetPassword  {...props}/>} />
        <Gaurd  path="/" name="Home" token='vt-music' routeRedirect="/login" component={TheLayout} />
            </Switch>
          </React.Suspense>
      </HashRouter>
    );
  }
}

export default App;
